const inspect = require('util').inspect
const { upRound, evenRound, format } = require('./utils')

const _pow = Math.pow
const _ceil = Math.ceil
const _floor = Math.floor
const _round = Math.round
const _abs = Math.abs

/**
 * 
 */
class Calculator {

  /**
   * @param {Number|String|Calculator} v 
   */
  constructor(v) {
    if (v) {
      if (v._vi === undefined) {
        const s = v.toString()
        const pe = s.lastIndexOf('e')
        if (pe > 0) { // 
          this._p = 0 - Number(s.slice(pe + 1))
          const p = s.indexOf('.')
          if (p < 0) {
            this._vi = Number(s.slice(0, pe))
          } else {
            this._p += pe - p - 1
            if (this._p < 0) {
              this._vi = Number(s) * _pow(10, this._p)
            } else {
              this._vi = Number(s.slice(0, p) + s.slice(p + 1, pe))
            }
          }
          this._v = null
        } else {
          const p = s.indexOf('.')
          if (p < 0) {
            this._vi = this._v = Number(v)
            this._p = 0
          } else {
            this._vi = Number(s.substr(0, p) + s.substr(p + 1))
            this._p = s.length - p - 1
            this._v = null
          }
        }
      } else {
        this._vi = v._vi
        this._p = v._p
        this._v = v._v
      }
    } else {
      this._vi = 0
      this._p = 0
      this._v = null
    }
  }

  /**
   * `+`
   * @param {Number|String} v 
   * @returns {Calculator} this
   */
  add (v) {
    if (v) {
      const s = v.toString()
      const pe = s.indexOf('e')
      let _p = 0
      let _vi
      if (pe > 0) {
        _p = -Number(s.slice(pe + 1))
        const p = s.indexOf('.')
        if (p < 0) {
          _vi = Number(s.slice(0, pe))
        } else {
          _vi = Number(s.slice(0, p) + s.slice(p + 1, pe))
          _p += pe - p - 1
        }
      } else {
        const pos = s.indexOf('.')
        if (pos < 0) {
          _vi = Number(v)
        } else {
          _vi = Number(s.substr(0, pos) + s.substr(pos + 1))
          _p = s.length - pos - 1
        }
      }
      if (_p > this._p) {
        this._vi = this._vi * _pow(10, _p - this._p) + _vi
        this._p = _p
      } else {
        this._vi = this._vi + _vi * _pow(10, this._p - _p)
      }
      this._v = null
    }
    return this
  }

  /**
   * `+`
   * @param {Calculator} v 
   */
  $add (v) {
    if (v._p > this._p) {
      this._vi = this._vi * _pow(10, v._p - this._p) + v._vi
      this._p = v._p
    } else {
      this._vi = this._vi + v._vi * _pow(10, this._p - v._p)
    }
    this._v = null
    return this
  }

  /**
   * `-`
   * @param {Number|String} v 
   */
  sub (v) {
    if (v) {
      const s = v.toString()
      const pos = s.indexOf('.')
      let _p = 0
      let _vi
      if (pos < 0) {
        _vi = Number(v)
      } else {
        _vi = Number(s.substr(0, pos) + s.substr(pos + 1))
        _p = s.length - pos - 1
      }
      if (_p > this._p) {
        this._vi = this._vi * _pow(10, _p - this._p) - _vi
        this._p = _p
      } else {
        this._vi = this._vi - _vi * _pow(10, this._p - _p)
      }
      this._v = null
    }
    return this
  }

  /**
   * `-`
   * @param {Calculator} v 
   * @returns {Calculator} this
   */
  $sub (v) {
    if (v._p > this._p) {
      this._vi = this._vi * _pow(10, v._p - this._p) - v._vi
      this._p = v._p
    } else {
      this._vi = this._vi - v._vi * _pow(10, this._p - v._p)
    }
    this._v = null
    return this
  }

  /**
   * `*`
   * @param {Number|String} v 
   * @returns {Calculator} this
   */
  mul (v) {
    if (v) {
      const s = v.toString()
      const pos = s.indexOf('.')
      let _p = 0
      let _vi
      if (pos < 0) {
        _vi = Number(v)
      } else {
        _vi = Number(s.substr(0, pos) + s.substr(pos + 1))
        _p = s.length - pos - 1
      }
      this._vi = this._vi * _vi
      this._p = this._p + _p
    } else {
      this._vi = 0
      this._p = 0
    }
    this._v = null
    return this
  }

  /**
   * `*`
   * @param {Calculator} v 
   * @returns {Calculator} this
   */
  $mul (v) {
    this._vi = this._vi * v._vi
    this._p = this._p + v._p
    this._v = null
    return this
  }

  /**
   * `/`
   * @param {Number|String} v 
   * @returns {Calculator} this
   */
  div (v) {
    if (v) {
      let s = v.toString()
      let pe = s.indexOf('e')
      let _p = 0
      let _vi
      if (pe > 0) { // 
        _p = -Number(s.slice(pe + 1))
        const p = s.indexOf('.')
        if (p < 0) {
          _vi = Number(s.slice(0, pe))
        } else {
          _vi = Number(s.slice(0, p) + s.slice(p + 1, pe))
          _p += pe - p - 1
        }
      } else {
        const pos = s.indexOf('.')
        if (pos < 0) {
          _vi = Number(v)
        } else {
          _vi = Number(s.substr(0, pos) + s.substr(pos + 1))
          _p = s.length - pos - 1
        }
      }
      this._vi = this._vi / _vi
      s = this._vi.toString()
      pe = s.indexOf('e') // 
      if (pe < 0) {
        const pos = s.indexOf('.')
        if (pos > 0) {
          this._vi = Number(s.substr(0, pos) + s.substr(pos + 1))
          this._p = this._p - _p + s.length - pos - 1
        } else {
          this._p -= _p
        }
      } else {
        const e = Number(s.substr(pe + 1))
        const pos = s.indexOf('.')
        // assert.ok(pos > 0, '0')
        // if (pos < 0) {
        //   this._vi = Number(s.substr(0, pe))
        //   this._p = this._p - _p - e
        // }
        this._vi = Number(s.substr(0, pos) + s.substring(pos + 1, pe))
        this._p = this._p - _p + pe - pos - 1 - e
      }
    } else {
      this._vi = Infinity
      this._p = 0
    }
    this._v = null
    return this
  }

  /**
   * `/`
   * @param {Calculator} v 
   * @returns {Calculator} this
   */
  $div (v) {
    this._vi = this._vi / v._vi
    let s = this._vi.toString()
    const pe = s.indexOf('e')
    if (pe < 0) {
      let pos = s.indexOf('.')
      if (pos < 0) {
        this._p = this._p - v._p
      } else {
        // // 0
        // let i = s.length - 1;
        // while (i > pos && s[i] === '0') i--;
        // i++;
        this._vi = Number(s.substr(0, pos) + s.substr(pos + 1))
        this._p = this._p - v._p + s.length - pos - 1
      }
    } else {
      const e = Number(s.substr(pe + 1))
      const pos = s.indexOf('.')
      // assert.ok(pos > 0, '0')
      // if (pos < 0) {
      //   this._p = this._p - v._p - e
      // }
      this._vi = Number(s.substr(0, pos) + s.substring(pos + 1, pe))
      this._p = this._p - v._p + pe - pos - 1 - e
    }
    this._v = null
    return this
  }

  max (v) {
    const s = v.toString()
    const pos = s.indexOf('.')
    let _p = 0
    let _vi
    if (pos < 0) {
      _vi = Number(v)
    } else {
      _vi = Number(s.substr(0, pos) + s.substr(pos + 1))
      _p = s.length - pos - 1
    }
    if (this._p > _p) {
      let _di = _vi * _pow(10, this._p - _p)
      if (_di > this._vi) {
        this._p = _p
        this._vi = _vi
        this._v = null
      }
    } else {
      let _di = this._vi * _pow(10, _p - this._p)
      if (_vi > _di) {
        this._p = _p
        this._vi = _vi
        this._v = null
      }
    }
    return this;
  }

  $max (v) {
    if (this._p > v._p) {
      let _di = v._vi * _pow(10, this._p - v._p)
      if (_di > this._vi) {
        return v
      }
    } else {
      let _di = this._vi * _pow(10, v._p - this._p)
      if (v._vi > _di) {
        return v
      }
    }
    return this
  }

  min (v) {
    const s = v.toString()
    const pos = s.indexOf('.')
    let _p = 0
    let _vi
    if (pos < 0) {
      _vi = Number(v)
    } else {
      _vi = Number(s.substr(0, pos) + s.substr(pos + 1))
      _p = s.length - pos - 1
    }
    if (this._p > _p) {
      let _di = _vi * _pow(10, this._p - _p)
      if (_di < this._vi) {
        this._p = _p
        this._vi = _vi
        this._v = null
      }
    } else {
      let _di = this._vi * _pow(10, _p - this._p)
      if (_vi < _di) {
        this._p = _p
        this._vi = _vi
        this._v = null
      }
    }
    return this;
  }

  $min (v) {
    if (this._p > v._p) {
      let _di = v._vi * _pow(10, this._p - v._p)
      if (_di < this._vi) {
        return v
      }
    } else {
      let _di = this._vi * _pow(10, v._p - this._p)
      if (v._vi < _di) {
        return v
      }
    }
    return this
  }

  /**
   * @returns {Number} 
   */
  v () {
    if (this._v === null) {
      if (this._p === 0) {
        this._v = this._vi
      } else {
        this._v = Number(this.vs())
      }
    }
    return this._v
  }

  /**
   * 
   */
  value () {
    return this.v();
  }

  vs () {
    if (this._p > 0) {
      if (this._vi < 0) {
        let s = this._vi.toString()
        let p = this._p - s.length + 1
        if (p < 0) {
          p = 1 - p
          return s.substring(0, p) + '.' + s.substring(p)
        } else {
          s = s.slice(1)
          while ((p--) > 0) {
            s = '0' + s
          }
          return '-0.' + s
        }
      } else {
        let s = this._vi.toString()
        let p = this._p - s.length
        if (p < 0) {
          p = 0 - p
          return s.substring(0, p) + '.' + s.substring(p)
        } else {
          while ((p--) > 0) {
            s = '0' + s
          }
          return '0.' + s
        }
      }
    } else if (this._p < 0) {
      let s = this._vi.toString()
      let p = 0 - this._p
      while ((p--) > 0) {
        s += '0'
      }
      return s
    }
    return this._vi.toString()
  }

  ve () {
    if (this._vi === 0) {
      return this._vi.toString()
    } else {
      let s = this._vi.toString()
      let i = s.length - 1
      let p = (i - this._p).toString()
      while (i > 0 && s[i] === '0') i--;
      i++;
      const sp = s[0] === '-' ? 2 : 1
      if (i > sp) {
        if (p[0] === '-') {
          return s.substr(0, sp) + '.' + s.substr(sp) + 'e' + p
        } else {
          return s.substr(0, sp) + '.' + s.substr(sp) + 'e+' + p
        }
      } else {
        if (p[0] === '-') {
          return s.substr(0, sp) + 'e' + p
        } else {
          return s.substr(0, sp) + 'e+' + p
        }
      }
    }
  }

  /**
   * (,`setup`)
   * @param {Number} precision 
   * @returns {Number} 
   */
  round (precision = 0) {
    return upRound(this._vi, this._p, precision)
  }
  /**
   * `round`
   * @see this.round
   * @param {Number} precision 
   * @returns {Number} 
   */
  rv (precision = 0) {
    return this.round(precision)
  }

  /**
   * 
   * @param {Number} precision 
   * @returns {Number} 
   */
  upRound (precision = 0) {
    return upRound(this._vi, this._p, precision)
  }

  /**
   * `upRound`
   * @see this.upRound
   * @param {Number} precision 
   * @returns {Number} 
   */
  uv (precision = 0) {
    return this.upRound(precision)
  }
  /**
   * 
   * @param {Number} precision 
   * @returns {Number} 
   */
  evenRound (precision = 0) {
    return evenRound(this._vi, this._p, precision)
  }
  /**
   * `evenRound`
   * @see this.evenRound
   * @param {Number} precision 
   * @returns {Number} 
   */
  ev (precision = 0) {
    return this.evenRound(precision)
  }

  /**
   * ()
   * @param {Number} precision 
   * @returns {Calculator} this
   */
  r (precision = 0) {
    return this.ru(precision)
  }

  /**
   * 
   * @param {Number} precision 
   * @returns {Calculator} this
   */
  ru (precision = 0) {
    if (this._p > precision) {
      let s = this._vi.toString()
      let p = s.length - this._p + precision // 
      if (p < 0) {
        this._vi = 0
        this._p = 0
      } else {
        this._vi = Number(s.substr(0, p)) + _round(Number('0.' + s.substr(p)))
        this._p = precision
      }
      this._v = null
    }
    return this
  }

  /**
   * 
   * @param {Number} precision 
   * @returns {Calculator} this
   */
  re (precision = 0) {
    if (this._p > precision) {
      let s = this._vi.toString()
      let p = s.length - this._p + precision // 
      if (p < 0) {
        this._vi = 0
        this._p = 0
      } else {
        this._vi = evenRound(Number(s.substr(0, p) + '.' + s.substr(p)), 0, 0)
        this._p = precision
      }
      this._v = null
    }
    return this
  }

  /**
   * 
   * @param {Number} precision 
   * @returns {Calculator} this
   */
  rc (precision = 0) {
    if (this._p > precision) {
      let s = this._vi.toString()
      let p = s.length - this._p + precision // 
      if (p < 0) {
        this._vi = 0
        this._p = 0
      } else {
        this._vi = Number(s.substr(0, p)) + _ceil(Number('0.' + s.substr(p)))
        this._p = precision
      }
      this._v = null
    }
    return this
  }

  /**
   * 
   * @param {Number} precision 
   * @returns {Calculator} this
   */
  rf (precision = 0) {
    if (this._p > precision) {
      let s = this._vi.toString()
      let p = s.length - this._p + precision // 
      if (p < 0) {
        this._vi = 0
        this._p = 0
      } else {
        this._vi = Number(s.substr(0, p))
        this._p = precision
      }
      this._v = null
    }
    return this
  }

  /**
   * 
   * @param {Number} precision 
   * @returns {Number} 
   */
  ceil (precision = 0) {
    if (this._p > precision) {
      const m = _pow(10, this._p - precision)
      return _ceil(this._vi / m) / _pow(10, precision)
    } else if (this._p < 0) {
      return this._vi * _pow(10, -this._p)
    } else {
      return this._vi / _pow(10, this._p)
    }
  }

  /**
   * 
   * @param {Number} precision 
   * @returns {Number} 
   */
  cv (precision = 0) {
    return this.ceil(precision)
  }

  /**
   * 
   * @param {Number} precision 
   * @returns {Number} 
   */
  floor (precision = 0) {
    if (this._p > precision) {
      const m = _pow(10, this._p - precision)
      return _floor(this._vi / m) / _pow(10, precision)
    } else {
      return this._vi / _pow(10, this._p)
    }
  }

  /**
   * 
   * @param {Number} precision 
   * @returns {Number} 
   */
  fv (precision = 0) {
    return this.floor(precision)
  }

  fmt (fmt = '#,##0.00', prefix = '', suffix = '') {
    return this.format(fmt, prefix, suffix)
  }

  format (fmt = '#,##0.00', prefix = '', suffix = '') {
    return format(this.v(), fmt, prefix, suffix)
  }

  thousands (precision = 2) {
    return format(this.rv(precision), '#,##0.', '', '', ',', precision);
  }

  /**
   * 
   * @param {*} currency 
   * @param {*} positiveSign 
   */
  currency (currency = '', positiveSign = false) {
    currency = currency || '';
    if (positiveSign && this._vi >= 0) {
      currency = currency + '+';
    }
    return format(this.rv(2), '#,##0.00', currency, '');
  }

  /**
   * 
   * @param {*} prefix 
   * @param {*} [precision=2] (2)
   * @returns {String} 
   */
  signed (prefix = '', precision = 2) {
    prefix = prefix || '';
    if (this._vi >= 0) {
      prefix = prefix + '+';
    }
    return format(this.rv(precision), '0.00', prefix, '', '', precision);
  }

  /**
   * 
   * @param {*} prefix 
   * @param {*} [precision=2] (2)
   * @returns {String} 
   */
  unsigned (prefix = '', precision = 2) {
    prefix = prefix || '';
    let v = this.rv(precision);
    if (this._vi < 0) {
      v = v.toString().slice(1);
    }
    return format(v, '0.', prefix, '', '', precision);
  }

  debug () {
    this._print(this._vi, this._p)
    return this
  }

  /**
   * 
   */
  valueOf () {
    return this.v()
  }

  /**
   * 
   */
  toString () {
    return this.vs()
  }

  /**
   * JSON.stringify
   */
  toJSON () {
    return this.v()
  }

  /**
   * inspectconsole`console.log($C(1))`
   */
  [inspect.custom] () {
    return this.v()
  }

  /**
   * 0
   */
  isZero () {
    return this._vi === 0
  }

  /**
   * 
   */
  positive () {
    return this._vi > 0
  }

  /**
   * 
   */
  negative () {
    return this._vi < 0
  }

  /**
   * 
   */
  abs () {
    this._vi = _abs(this._vi)
    this._v = null
    return this;
  }

}

class StrictCalculator extends Calculator {
  constructor(v) {
    super(v)
    if (!(v && v._vi)) {
      this.check(v)
    }
  }

  check (v) {
    if (isNaN(v)) throw new Error(`Invalid number[${v}]`)
  }

  $check (v) {
    if (typeof v._vi === 'undefined') throw new Error(`Invalid calculator[${v}]`)
  }

  add (v) {
    this.check(v)
    return super.add(v)
  }

  $add (v) {
    this.$check(v)
    return super.$add(v)
  }

  sub (v) {
    this.check(v)
    return super.sub(v)
  }

  $sub (v) {
    this.$check(v)
    return super.$sub(v)
  }

  mul (v) {
    this.check(v)
    return super.mul(v)
  }

  $mul (v) {
    this.$check(v)
    return super.$mul(v)
  }

  div (v) {
    this.check(v)
    return super.div(v)
  }

  $div (v) {
    this.$check(v)
    return super.$div(v)
  }
}

function calculator (v) {
  return new calculator.Calculator(v)
}

function strict (v) {
  return new StrictCalculator(v)
}

function withStrict () {
  calculator.Calculator = StrictCalculator;
  return this;
}

function withoutStrict () {
  calculator.Calculator = Calculator;
  return this;
}

Calculator.prototype._print = console.error.bind(console, '[debug]')

calculator.setDebug = function (print) {
  Calculator.prototype._print = print
}

calculator.resetDebug = function () {
  Calculator.prototype._print = console.error.bind(console, '[debug]')
}

calculator.Calculator = Calculator
calculator.StrictCalculator = StrictCalculator;
/**
 * 
 */
calculator.$ = calculator
calculator.strict = strict;
calculator.withStrict = withStrict;
calculator.withoutStrict = withoutStrict;
module.exports = calculator